@import 'variable';

.project_ctn {
	margin: 0 10%;
	color: $darkgrey;
	font-size: 13px;
}
.project_section {
	margin: 3rem 0 0 0;
}
.cover_div {
	height: 250px;
	overflow: hidden;
	position: relative;
	background-color: grey;
}
.cover_img {
	width: 100%;
}

.project_title {
	text-transform: uppercase;
	font-size: 30px;
}

.banner {
	background: rgba(255, 255, 255, 0.6);
	position: absolute;
	top: 150px;
	display: flex;
	width: 100%;
	justify-content: space-around;
	padding: 10px 0;
	place-items: center;
}

.cover_button {
	position: absolute;
	top: 10px;
	right: 10px;
}

.situation_grid {
	display: grid;
	grid-template-columns: 70% 20%;
	grid-column-gap: 6%;
}

.summary_div {
	grid-area: 1 / 1 / 2 / 2;
	margin: 10px;
	height: auto;
}
.stats_div {
	grid-area: 2 / 1 / 3 / 2;
	display: flex;
	align-items: center;
	justify-content: center;
}
.user_div {
	grid-area: 1 / 2 / 3 / 3;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
}

.profile_pic {
	height: auto;
	width: 250px;
	border-radius: 40px;
	margin: 1em;
}

.name {
	font-weight: 600;
	font-size: 16px;
}

.h1 {
	font-weight: 500;
	font-size: 32px;
	margin: 30px 0 20px 0;
}
.stats {
	background: $pink;
	color: $darkpink;
	width: 140px;
	height: 100px;
	border-radius: 25px;
	padding: 0.5rem 1rem;
	margin: 1rem;
	display: flex;
	flex-direction: column;
	place-items: center;
}
.stats_text {
	color: $darkgrey;
	line-height: 2;
	font-weight: 600;
	text-align: center;
}
.details {
	font-style: italic;
	font-size: 15px;
	font-weight: 400;
}
.left_margin {
	margin-left: 1em;
}
.top_margin {
	margin-top: 2rem;
}
.icon_text {
	display: inline-flex;
	align-items: center;
}
.conclusion_grid {
	display: grid;
	grid-template-columns: 1fr 4fr;
}
.conclusion_links {
	grid-column: 1 / 2;
}
.conclusion {
	grid-column: 2 / 3;
}
.center {
	justify-content: center;
	display: flex;
}
.preview_end {
	margin-top: -100px;
}
